.footer {
    background: #3b4e51;
    height: 80px;
    color: white;
    &-content {
        max-width: 1170px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
    }   
    li {
        list-style: none;
        margin-right: 30px;
    }
    a {
        color: white;
        text-decoration: none;
    }
    ul {
        display: flex;
        li a:hover {
            color: #f3f3f3;
        }
    }
}

@media (max-width: 1200px) {
    .footer-content {
        padding: 0 20px;

    }
} 

