.right-menu {
 
 ul {
  display: flex;
  align-items: center;
   li {
  list-style: none;
  margin-left: 15px;&:hover {
  background-color: #3b4e51;
  color: white;
}

&:focus {
  color: white;
}

.right-menu-link {
  color: black;
  text-decoration: none;
  font-size: 16px;
  padding: 10px 15px;
  display: block;

  &:hover {
    color: white;
  }
}
}
}

.header-burger {
display: none;
}

.menu-bar {
display: none;
}
}


@media (max-width: 1200px) {
.right-menu {
ul {
li {
  margin-left: 5px;

  .right-menu-link {
    font-size: 14px;
    padding: 5px 10px;
    text-align: center;

  }
}
}
}
}

@media only screen and (max-width: 767px) {
  .right-menu {

    .header-burger {
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
    }

    .header-burger span {
      position: absolute;
      background-color: black;
      width: 100%;
      height: 2px;
      left: 0;
      top: 9px;
      transition: all 0.3s ease 0s;
    }

    .header-burger:before,
    .header-burger:after {
      content: '';
      background-color: black;
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
      transition: all 0.3s ease 0s;
    }

    .header-burger:before {
      top: 0;
    }

    .header-burger:after {
      bottom: 0;
    }

    .header-burger {
      &.active span {
        transform: scale(0);
      }
    }

    .header-burger {
      &.active:before {
        transform: rotate(45deg);
        top: 9px;
      }
    }

    .header-burger {
      &.active:after {
        transform: rotate(-45deg);
        bottom: 9px;
      }

    }

    .menu-bar {
      display: block;
    }

    ul {
      display: none;

      li {
        margin-left: 0;
        width: 150px;
      }

      &.open {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        left: 0;
        background-color: white;
        padding: 20px;
        top: 80px;
        box-sizing: border-box;
        border-top: 1px solid silver;
      }
    }
  }
}


