.main{
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 20px;
    &-title {
        color: black;
        font-weight: bold;
        margin: 30px 0;
    }
    &-content {
        text-align: justify;
        margin-bottom: 30px;
    }

    .updated.custom-updated {
        font-size: 18px;
        font-weight: bold!important;
        color: #43585b;
        font-style: italic;
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    
    .updated span {
        color: #0dc342;
    }
}